@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
body {
  margin: 0;
  box-sizing:border-box;
}

body{margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif;color:#373737}.light{color:#373737;background:#FFF}.dark{color:#FFF;background:rgba(0,0,0,0.7)}.dark a{color:#FFF}a,p{padding:0;margin:0}li{margin-bottom:1rem}.container{margin:4rem}.date{font-weight:normal;font-style:italic}form{margin:20px 0}.resume-preview{border:solid 1px #373737;width:1159px;height:1500px;padding:4rem}.resume-preview .section{margin-top:1rem;padding:1rem}.resume-preview .picture-name{display:flex;align-items:flex-end}.resume-preview .picture-name .profile-picture{width:200px;object-fit:contain}.resume-preview .picture-name .picture-name-right{margin:0 2rem}.resume-preview .picture-name .picture-name-right h1{font-size:48px}.resume-preview .picture-name .picture-name-right h2{font-size:24px}.resume-preview .section-socials{background-color:rgba(0,0,0,0.1)}.resume-preview .section-socials>div{display:flex}.resume-preview .section-socials .contact-info{margin-bottom:1rem;justify-content:space-between}.resume-preview .section-socials .social-media p{margin-right:2rem}.resume-preview .panels{display:grid;grid-template-columns:60% 40%}

